.form-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.form-container label {
  text-align: left;
}

.form-group {
  margin-bottom: 20px;
}

input[type="text"],
select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 5px;
}

input[type="radio"] {
  margin-right: 5px;
}


@media screen and (max-width: 600px) {
  input[type="text"],
  select {
    width: 100%;
  }
}


.checkbox-list {
  margin-bottom: 10px;
}

.checkbox-scrollable {
  max-height: 150px; /* Adjust height as needed */
  overflow-y: auto;

}

.checkbox-scrollable > div {
  margin-bottom: 1px;
}

/* policy.css */





.close-btn {
position: absolute;
margin: 0rem 0 0 35rem;
color: #ffff;
font-size: 30px;
}


.close-btn:hover {
color: #ccc;
}
