.login-container {
    max-width: 600px;
    margin: 100px auto;
    padding: 20px;
    border: 1px solid #ccc;
   
 
  }

  .login-container h2 {
    color: #000;
    margin-left: 10%;
  }

  .login-form {
    margin-top: 10rem;
    border: 1px solid #ccc;
    border-radius: 10px;
    /* background-image: url('../../Image/backup1.jpg'); */
    background-repeat: no-repeat;
    background-size: cover
  }

  .login p {
    color: #000;
    margin: 2.8rem 0 0 1rem;
  }

  
  .login button {
    width: 101px;
    color: #000;
    margin: 2rem 0 2rem 15rem;
    background: none;
  }

  .login button:hover {
      background: none;
  }
    
  
  h1, h2 {
    color: #ffff;
    margin: 2rem;
    text-align: center;
    margin-bottom: 10px;
  }
  
  .login-form input[type="text"],
  .login-form input[type="password"] {
    width: 80%;
    padding: 10px;
    margin: 10px 3rem;
    border: 2px solid #ffff;
    border-radius: 5px;
    box-sizing: border-box;
    color: #ffff;
    background: none;
  }


  .login-form ::placeholder {
    color: #ffff;
  }
  
  input[type="submit"] {
    width: 60%;
    padding: 10px;
    margin-top: 10px;
    background-color: #277db0;
    color: white;
    border: none;
    margin: 10px 6rem;
    border-radius: 3px;
    cursor: pointer;
  }

  input[type="submit"]:hover {
    width: 60%;
    padding: 10px;
    margin-top: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    margin: 10px 6rem;
    border-radius: 3px;
    cursor: pointer;
  }
  .error {
    color: red;
    margin-bottom: 10px;
  }
  
   .login{
    display: flex;
   }
   .lb{
    background-color: rgb(164, 61, 180);
    color: aliceblue;
   }
   .error{
    background-color: bisque;
   }

   .logout-btn {
    display: flex;
    justify-content: center;
   }

   .signup button {
    width: 80px;
    color: #000;
    margin: 1rem;
    background: none;
  }

  .signup button:hover {
    border-radius: 5px;
    border: 1px solid #000;
  }
 
