.failed-container {
  display: grid;
  position: absolute;
  margin-top: 10rem;
  margin-left: 7rem;
}

.failed-container select {
  width: 37%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: -7%;
  margin-left: 60%;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-indicator {
  font-size: 35%;
}


.node-cards-container{
  display: flex;
  justify-content: space-around; 
  align-items: flex-start; 
  flex-wrap: wrap;
  top: 0;
  margin-left: -1rem;
}

.node-cards{
  width: auto;
  height: auto;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 1rem 0rem 0rem 0rem;
  border-radius: 3px;
  background-color: #ffff;
  cursor: pointer;
  font-size: 25px;
  box-shadow: 1px 5px 8px #ccc;
}

