.total-card-Container{
  width: 100%;
  display: flex;
  text-align: center;
  height: auto;
  margin-top: 1rem;
  align-items: center;
  justify-content: center;

}
.total-card {
  display: flex;
  flex-direction: column;
  width: 60%;
  height: 100%;
  border: 1px solid #ccc;
  padding: 10px;
  /* margin: -7rem 0rem 0rem 34.5rem; */
  border-radius: 3px;
  background-color: #ffff;
  cursor: pointer;
  font-size: 25px;
  /* z-index: 1; */
  box-shadow: 1px 5px 8px #ccc;
  /* justify-content: space-between; */
}
.total-card .total-card-title{
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 2rem;
  gap: 1rem;
}
.total-card .total-card-title h3, h4{
  display: flex;
  margin: 0;
  align-items: center;
}


.total-card .pie-chart {
  /* margin-left: 8rem; */
  width: 680px;
  height: 100%;
}
.total-line-chart{
  width: 100%;
  height: 100%;
  /* border: 1px solid; */
}

.Circle-Chart{
  width: 390px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  /* margin-left: 1.5rem; */
  border-right: 2px solid #ccc;
}
.ciclechart{
  height: 100%;
  width: 100%;
  /* margin-top: 10px;
  margin-left: 1rem; */
}
 .total-card-Box{
  display: flex;
  justify-content: space-between;
  height: 200px;
 }


.node-cards span {
  color: green;
  float: right;
  font-size: 25px;
}

.node-cards h3 {
  justify-content: right;
  font-size: 25px;
}

.count-view {
  display: flex;
  justify-content: center;
}

.count-view p {
  justify-content: center;
  margin-top: 5.4rem;
  margin-left: 7rem;
}

.node-cards button {
  height: 5%;
  color: #000;
  border: 1px solid #000;
  background: none;
  margin-left: 1rem;
  margin-top: 5rem;
}

.node-cards button:hover {
  color: #ffff;
  background-color: #277DB0;
}
.popup-jobs {
  width: 80%;
  height: 55%;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow: scroll;
}

.popup-jobs .close-btn {
  position: absolute;
  margin: -5rem 0 0 45rem;
  color: #ffff;
  font-size: 30px;
}


.popup-jobs .close-btn:hover {
  color: #ccc;
}


.pie-chart{
  box-sizing: border-box;
  display: block;
  height: 200px;
  width: 200px;
  margin: 0 0 0 1rem ;
}

