.menus-page {
    position: absolute;
    padding: 0;
    margin: -4rem;
    margin-left: 0rem;
    z-index: -1;
}



.menu-button {
  color: #277DB0;
  background-color: #ffff;
  padding: 10px 15px;
  margin: 5px;
  border: 1px solid #277DB0;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
}

.menu-button:hover {
  background-color: #277DB0;
  color: #ffff;
}

.menu-button.active {
  background-color: #277DB0;
  color: #ffff;
}
  

@media screen and (max-width: 667px) {
    .table {
        margin: 20rem 0rem 0rem 0.4rem;
        font-size: 4px;
        top: 0px;
        position: absolute;
        z-index: -1;
    }
    

    
    .menus-page {
        display: flex;
        flex-direction: column;
        margin: 3rem;
      }

    table, tr, th, td {
        border-collapse: collapse;
        padding: 10px;
        font-size: 3.5px;
    }

    table, tr, td {
        overflow-x: auto;
        max-width: 100%;
        border-collapse: collapse;
        padding: 10px;
        font-size: 5px;
        color: #000;
    }
}

.error{
  width: 30%;
  
}

.SearchF input[type="text"] {
  flex: 1; 
  margin-right: 10px; 
  width: 40%;
  border: 1px solid #000;

}