}

.timeselect{
  display: flex;
  justify-content: center;
}

.canvas{
  
  height: 300px !important;
}

.nodes-cards {
  display: none;
}

.nodes-cards.selected {
  display: block;
  background-color: #ffff;
}

.nodes-cards button {
  height: 5%;
  color: #000;
  border: 1px solid #000;
  background: none;
  margin-left: 1rem;
  margin-top: 5rem;
}

.nodes-cards button:hover {
  color: #ffff;
  background-color: #277DB0;
}

.nodes-cards {
  width: auto;
  height: auto;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 1rem 0rem 0rem 0rem;
  border-radius: 3px;
  background-color: #ffff;
  cursor: pointer;
  font-size: 25px;
  box-shadow: 1px 5px 8px #ccc;
}


