.popups-jobs .success-close-btn {
  position: absolute;
  margin-left: 70%;
  color: #ffff;
  font-size: 30px;
}

.popups-jobs .close-btn:hover {
  color: #ccc;
}

.bar-chart-container {
  width: auto;
}

.popup-jobs .close-btn {
  position: absolute;
  color: #ffff;
  font-size: 30px;
}


.popup-jobs .close-btn:hover {
  color: #ccc;
}


.pie-chart {
  box-sizing: border-box;
  display: block;
  height: 200px;
  width: 200px;
  margin: 0 0 0 1rem;
}



.menus-page {
  position: absolute;
  padding: 0;
  margin-top: 11rem;
  margin-left: 8rem;

}



.menu-button {
  color: #277DB0;
  background-color: #ffff;
  padding: 10px 15px;
  margin: 5px;
  border: 1px solid #277DB0;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
  width: 100%;
}

.menu-button:hover {
  background-color: #277DB0;
  color: #ffff;
}

.menu-button.active {
  background-color: #277DB0;
  color: #ffff;
}
.jobsuss-container{
  height: fit-content;
  width: auto;
  display: flex;
  flex-direction: column;
  margin: 10px;
  margin-top: 2rem;
}
.jobsuss-container .jobsuss-container-btn{
  height: 20%;
  color: #000;
  border: 1px solid;
  background: none;
  margin: 5px;
}

.card-view-more-btn button {
  color: #000000d3;
  margin-left: 45%;
  background: none;
  margin-top: 1rem;
  padding: 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.card-view-more-btn button:hover {
  color: #ffff;
  background-color: #277DB0;
}

@media screen and (max-width: 667px) {
  .menus-page {
    margin: 8.5rem 1rem 0rem 3rem;
  }

  .menus-page ul li {
    display: flex;
    flex-direction: column;
    width: 160%;
    text-align: center;
  }
}



 
.SearchJ input[type="text"] {
  flex: 1; 
  margin-right: 10px; 
  width: 40%;
  border: 1px solid #000;
}



.dwnldbtn {
  width: 20%;
  padding: 10px 20px;
  border: 1px solid #000000d3;
  border-radius: 5px;
  background-color: white;
  color: #000000d3;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, color 0.3s;
}


.dwnldbtn :hover {
  color: #ffff;
  background-color: #000000d3;
}

