/* Numone.css */

.error-message {
color: red;
margin-top: 5px; /* Add some spacing above the error message */
}

.Numthr{
width: 100%;
height: 250px;
margin-left: 0%;
padding: 20px;
border: 1px solid #ccc;
border-radius: 5px;
background-color: #ffff;
overflow: scroll;
}

.source-data-form {
text-align: justify;
max-width: 400px;
}

.storage-type-cards {
display: flex;
flex-wrap: wrap;
margin-top: 1rem;
gap: 1rem;
}



.storage-type-card {
border-radius: 5px;
padding: 10px;
cursor: pointer;
display: flex;
align-items: center;
margin-bottom: 10px;
justify-content: center;
gap: 10px;
width: 11.3rem;
background-color: #ffff;
border: 1px solid #ccc;
}

.storage-type-card:hover {
box-shadow: 1px 3px 5px #808080;
}


.storage-type-card img {
max-width: 18%;
height: auto;
}

.storage-type-card.selected {
box-shadow: 1px 3px 5px #808080;
background-color: #eaf248;
}

.sourceB-btn  {
padding: 10px 20px;
border: 1px solid #000000d3;
border-radius: 5px;
background-color: white;
color: #000000d3;
cursor: pointer;
font-size: 16px;
transition: background-color 0.3s, color 0.3s;
}

.sourceB-btn.active,
.sourceB-btn:hover {
background-color: #000000d3;
color: white;

}

.popup-backup {
width: calc(95% - 6rem);
height: 85%;
background-color: #fff;
padding: 20px;
border-radius: 5px;
margin-top: 4rem;
margin-left: 6rem;
box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
overflow-y: scroll;
}

.popup-backup .backup-h2 {
width: 100%;
text-align: center;
color: #000;
font-weight: bolder;
margin: 0;
/* margin-top: 2rem; */
}

.popup-backup .agent-searchB {
width: 100%;
height: 2.8rem;
border-radius: 5rem;
/* box-shadow: 1px 3px 5px #808080; */
padding: 5px 15px;
margin: 0
}

.agent-searchB:focus{
box-shadow: 1px 1px 5px #808080;
}

.agent-SearchIcon{
position: relative;
margin: 0;
margin-left: -3rem;
font-size: 1.5rem;
}


.popup-backup .agent-searchB-btn {
width: 35%;
display: flex;
align-items: center;
justify-content: space-between;
font-size: 20px;
/* gap: 1rem; */
margin: 0;
}


.restore-closeB-btn {
/* position: absolute; */
/* margin-left: 60rem; */
/* margin-top: -5rem; */
color: #000;
font-size: 30px;
}


.Agent-headerContainer{
width: 100%;
display: flex;
align-items: center;
justify-content: space-between;
margin-top: 1.5rem;
/* border: 1px solid; */
}

.agent-search{
display: flex;
align-items: center;
width: 60%;
}

.agent-device-view button {
color: #000;
margin: 0px;
/* margin-top: 5px; */
border-radius: 1px;
background: none;
border: 1px solid #ccc;
}
.agent-device-view button:hover {
color: #ffff;
background-color: #277Db0;
box-shadow: 1px 3px 5px #808080;
}
.agent-device-view .device-toggle-btn1 {
border-bottom-left-radius: 5rem;
border-top-left-radius: 5rem;
}

.agent-device-view .device-toggle-btn {
border-bottom-right-radius: 5rem;
border-top-right-radius: 5rem;
}

.Agent-icon-view{
/* position: absolute; */
/* border: 1px solid; */
width: 100%;
max-height: 72%;
overflow-y: auto;
display: flex;
flex-wrap: wrap;
/* margin-right: 5rem; */
margin-top: 2rem;
gap: 2rem;
/* border: 1px solid; */
}


.Agent-list-view-grid  {
width: 100%;
padding: 2px;
overflow-y: scroll;
}

.Agent-list-view-grid  tbody tr:hover {
background-color: #fcf7b6;
box-shadow: 1px 3px 5px #808080;
cursor: pointer;
}

.Agent-list-view-grid  tbody .selected{
background-color: #eaf248;
box-shadow: 1px 3px 5px #808080;
cursor: pointer;
}

.Agent-system-selector-buttons{
/* position: absolute; */
bottom: 2rem;
width: 100%;
text-align: center;
/* border: 1px solid; */
}
.Agent-icon-view .AgentLoader{
margin-top: -1rem;
height: 60%;
margin-left: 18rem;
max-width: 50%; 
}



