.popup-containera {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    width: 500px; /* Adjust width as needed */
  }
  
  .popup-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .popup-title {
    font-size: 18px;
  }
  
  .close-button {
    border: none;
    background: none;
    cursor: pointer;
    font-size: 20px;
    color: #333;
  }
  
  .popup-content {
    margin-top: 10px;
  }
  
  .form-group {
    margin-bottom: 10px;
  }
  
  .test-connection-button {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .connection-result {
    margin-top: 10px;
  }
  
  
  .form-group p{
    cursor: pointer;
    color: #007bff;
  }