/* Rtable.css */

/* Styles for the table */
table {
width: 100%;
border-collapse: collapse;
}

th, td {
border: 1px solid #ddd;
padding: 8px;
}

th {
background-color: #f2f2f2;
font-weight: bold;
text-align: left;
}

/* Styles for the running jobs section */
.running-jobs {
margin-top: 20px;
}

.running-jobs h2 {
font-size: 20px;
margin-bottom: 10px;
}

/* Styles for job rows */
.job-row {
background-color: #f9f9f9;
}

.job-row:hover {
background-color: #f0f0f0;
}
