/* -----------------search bar style start------------------------- */

.searchbar {
  width: 400%;
  background-color: black;
  display: flex;
  position: fixed;
  top: 0;
  margin: 0rem 0 0 0;
  height: 4rem;
  z-index: 100;

}



.searchbar .menu-icon {
  color: #ffff;
  display: none;
}

 .search-container {
  position: relative;
  display: inline-block;
}

.search-icon {
  cursor: pointer;
  color: #fff;
  margin: 1rem;
  font-size: 25px;
  margin-left: 65rem;
}

.searchbar .search-input {
  position: absolute;
  width: 45%;
  height: 2.5rem;
  margin: -.5rem -40rem;
}

.searchbar button {
  position: absolute;
  justify-content: center;
  margin: -.5rem 0rem 0 -50rem;
}

.search-input:focus {
  width: 200px;
  outline: none;

}

.searchbar .bell {
  color: #ffff;
  margin: 1rem;
  font-size: 25px;
}

.searchbar .profile {
  font-size: 25px;
  border-radius: 50%;
  margin: 1rem;

}

.searchbar ul li a {
  margin: 1rem 0rem;
  display: flex;
  color: #ffff;
}

/* -----------------search bar style end------------------------- */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}


.menu-btn {
  color: white;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 260px;
  background: #fff;
  z-index: 10;
  transition: all 0.5s ease;
}

.sidebar.close {
  width: 78px;
  box-shadow: 5px 10px 18px #888888;
  transition: all 0.01s;
}

.sidebar .logo-details {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
}

.sidebar .logo-details i {
  font-size: 30px;
  color: black;
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
}

.sidebar .logo-details .logo_name {
  font-size: 22px;
  color: #fff;
  font-weight: 600;
  transition: 0.3s ease;
  transition-delay: 0.1s;
}

.sidebar.close .logo-details .logo_name {
  transition-delay: 0s;
  opacity: 0;
  pointer-events: none;
}

.sidebar .nav-links {
  height: 100%;
  padding: 130px 0 150px 0;
  overflow: auto;
  margin-top: rem;
}

.sidebar.close .nav-links {
  overflow: visible;
}

.sidebar .nav-links::-webkit-scrollbar {
  display: none;
}

.sidebar .nav-links li {
  position: relative;
  list-style: none;
  transition: all 0.4s ease;
}

.sidebar .nav-links li:hover {
  background: white;
}

.sidebar .nav-links li .iocn-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sidebar.close .nav-links li .iocn-link {
  display: block;
}

.sidebar .nav-links li i {
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
  color: black;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.sidebar .nav-links li.showMenu i.arrow {
  transform: rotate(-180deg);
}

.sidebar.close .nav-links i.arrow {
  display: none;
}

.sidebar .nav-links li a {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.sidebar .nav-links li a .link_name {
  font-size: 18px;
  font-weight: 400;
  color: black;
  transition: all 0.4s ease;
}

.sidebar.open .nav-links li a .link_name1 {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.sidebar.close .nav-links li a .link_name1 {
  visibility: hidden;
}

.sidebar.close .nav-links li a:hover .link_name1 {
  position: fixed;
  margin-left: 4rem;
  padding-top: 5px;
  border-radius: 5px;
  background-color: #000;
  color: #fff;
  height: 2rem;
  width: 4rem;
  text-align: center;
  align-items: center;
  justify-content: center;
  visibility: visible;

}
.sidebar.open .nav-links li a .left{
  visibility: hidden;
  opacity: 0;
}

.sidebar.close .nav-links li a .left {
  visibility: hidden;
}
.sidebar.close .nav-links li a:hover .left{
  margin-left: 3.5rem;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-right: 13px solid #000;
  border-bottom: 10px solid transparent;
  position: fixed;
  visibility: visible;
}


.sidebar .nav-links li .sub-menu {
  padding: 6px 6px 14px 80px;
  margin-top: -10px;
  background: white;
  display: none;
}

.sidebar .nav-links li.showMenu .sub-menu {
  display: block;
}

.sidebar .nav-links li .sub-menu a {
  color: black;
  font-size: 15px;
  padding: 5px 0;
  white-space: nowrap;
  opacity: 0.6;
  transition: all 0.3s ease;
}

.sidebar .nav-links li .sub-menu a:hover {
  opacity: 1;
}

.sidebar.close .nav-links li .sub-menu {
  position: absolute;
  left: 100%;
  top: -10px;
  margin-top: 0;
  padding: 10px 20px;
  border-radius: 0 6px 6px 0;
  opacity: 0;
  display: block;
  pointer-events: none;
  transition: 0s;
}

.sidebar.close .nav-links li:hover .sub-menu {
  top: 0;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.4s ease;
}

.sidebar .nav-links li .sub-menu .link_name {
  display: none;
}

.sidebar.close .nav-links li .sub-menu .link_name {
  font-size: 18px;
  opacity: 1;
  display: block;
  font-size: medium;
}

.sidebar .nav-links li .sub-menu.blank {
  opacity: 1;
  pointer-events: auto;
  padding: 3px 20px 6px 16px;
  opacity: 0;
  pointer-events: none;
}

.sidebar .nav-links li:hover .sub-menu.blank {
  top: 50%;
  transform: translateY(-50%);
}

.sidebar .profile-details {
  position: fixed;
  bottom: 0;
  width: 260px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  padding: 12px 0;
  transition: all 0.5s ease;
}

.sidebar.close .profile-details {
  background: none;
}

.sidebar.close .profile-details {
  width: 78px;
}

.sidebar .profile-details .profile-content {
  display: flex;
  align-items: center;
}

.sidebar .profile-details img {
  height: 52px;
  width: 52px;
  object-fit: cover;
  border-radius: 16px;
  margin: 0 14px 0 12px;
  background: #1d1b31;
  transition: all 0.5s ease;
}

.sidebar.close .profile-details img {
  padding: 10px;
}

.sidebar .profile-details .profile_name,
.sidebar .profile-details .job {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  white-space: nowrap;
}

.sidebar.close .profile-details i,
.sidebar.close .profile-details .profile_name,
.sidebar.close .profile-details .job {
  display: none;
}

.sidebar .profile-details .job {
  font-size: 12px;
}

.open .sidebar.close {
  box-shadow: none;
}

.open {
  left: 0;
}

.open .sidebar .nav-links {
  width: 10rem;

}

.sidebar.open {
  width: 10.1rem;
  box-shadow: 5px 10px 18px #888888;
  transition: all 0.01s;
}

.sidebar.open .nav-links {
  height: 100%;
  padding: 30px 0 150px 0;
  overflow: hidden;
  margin-top: 8rem;
}


.sidebar.open .nav-links li .sub-menu {
  position: fixed;
  left: 100%;
  width: 100%;
  top: -10px;
  margin-top: 0;
  padding: 10px 20px;
  border-radius: 0 6px 6px 0;
  opacity: 0;
  display: contents;
  pointer-events: fill;
}

.sidebar.open .nav-links .sub-menu li {
  margin-left: 3.5rem;
  margin-top: -2.7rem;
  gap: 1rem;
  width: 5rem;
}

.home-section {
  position: relative;
  background: #E4E9F7;
  height: 100vh;
  left: 260px;
  width: calc(100% - 260px);

  z-index: 1;
}

.sidebar.close~.home-section {
  left: 63px;
  width: calc(100% - 78px);
  z-index: 10;
}

.home-section .home-content {
  height: 60px;
  display: flex;
  margin-top: 4.6rem;
  align-items: center;
}

.home-section .home-content .bx-chevron-right,
.home-section .home-content .text {
  color: black;
  font-size: 35px;
}

.home-section .home-content .bx-chevron-right {
  margin: 0px;
  cursor: pointer;
  background-color: #007bff;
  border-radius: 50%;
  color: #fff;
}

.home-section .home-content .bxs-pin {
  margin: 7px 0px 0px 48px;
  cursor: pointer;
  font-size: 10px;
  background-color: #007bff;
  border-radius: 50%;
  color: #fff;
}

.sidebar.close .bxs-pin {
  visibility: hidden;
  transition: all 0.01s;
  font-size: 10px;

}

.sidebar.open .bxs-pin {
  visibility: visible;
  transition: all 0.01s;
  font-size: 10px;
}

.sidebar.close .bx-chevron-right {
  visibility: visible;
  transition: all 0.01s;
}

.sidebar.open .bx-chevron-right {
  visibility: hidden;
  transition: all 0.01s;
}

.home-section .home-content .text {
  font-size: 26px;
  font-weight: 600;
}

.section.containerr.pinned {
  margin-left: 5rem;
}

.jobs-containerr.pinned {
  margin-left: 5rem;
}

