Reset styles
* {
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
  }
  
  /* Global styles */
  /* html,
  body {
    background-color: #dedede;
  } */
  
  .grid container {
    display: flex;
  }
  
  .grid-container,
  h2 {
    display: flex;
    justify-content: center;
    margin: 0rem 0rem 0rem 0rem;
    font-weight: 300;
    color: #568301;
  }
  
  .grid-container,
  h5 {
    font-weight: 300;
    color: #292929;
    /* margin: -1.9rem 0rem 0rem 17rem; */
  }
  
  .head-check {
    display: flex;
    margin: 1rem 0rem 0rem -49rem;
  }
  
  .file-check {
    float: left;
    margin: .1rem 0 0 0;
  }
  
  .input-path {
    display: flex;
    width: 63%;
    margin: 0rem 0rem 0rem 15rem;
  }
  
  .input-path button {
    height: 45.2px;
    position: absolute;
    margin: 0.3rem 0rem 0rem 44.4rem;
  }
  
  .input-expression {
    display: flex;
    width: 58%;
    height: 45px;
    gap: .5rem;
    margin: 0rem 0rem 0rem 15rem;
  }
  
  .input-expression button {
    height: 38px;
    position: absolute;
    margin: 0.2rem 0rem 0rem 48rem;
  
  }
  
  .input-expression select {
    display: flex;
    width: 40%;
    height: 35px;
    border-radius: 5px;
    margin: 0.3rem 0rem 0rem 0rem;
  }
  
  .content-section h2 {
    margin-left: -15rem;
  }
  
  .content-section p {
    display: flex;
    /* justify-content: center; */
    margin: -1.8rem 28rem 0rem 17rem;
    font-weight: 300;
    color: 568301;
  }
  
  .add-btn {
    color: #007bff;
    display: flex;
    background: none;
    border: none;
    cursor: pointer;
    justify-content: center;
    margin-left: -45rem;
  }
  
  
  
  .file-size {
    width: 30%;
    display: flex;
    justify-content: center;
    margin: -3rem 0rem 0rem 25rem;
  
  }
  
  .file-size select {
    height: 40px;
    width: 100px;
    margin: .3rem 0rem 0rem .5rem;
  }
  
