  /* Global styles */
  
  
  .file-browseer {
    height: 200px;
    background-color: #fff;
    width: 380px;
    overflow-x: scroll;
    border: 1px solid #292929;
    box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, .11);
  }
  
  /* .folder-contents {
  left: 20px;
  top: 100%;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 1px 1px 1px 0 rgba(0,0,0,.11);
  overflow-y: auto; 
  } */
  
  
  .btn {
    margin-bottom: 10px;
  }
  
  .folder-contents {
    padding-left: 20px;
  }
  
  .tree-folder-header {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .tree-folder-header-div {
    display: flex;
  }
  
  .tree-folder-header .arrow {
    margin-right: 5px;
  }
  
  .tree-folder-header input[type="checkbox"] {
    margin-right: 5px;
    margin-top: 8px;
    width: 20px;
  }
  
  .folder-icon {
    margin-right: 5px;
  }
  
  .tree-folder {
    margin-bottom: 10px;
    font-size: 14px;
    transition: all .4s ease;
    position: relative;
  }
  
  .tree-folder:hover {
    color: rgb(242, 116, 53);
  }
  
  .folder-icon {
    color: rgb(242, 176, 53);
  }
  
  
  
  .selected {
    background-color: #f0f0f0;
  }
  
  .tree-file {
    display: flex;
    align-items: center;
  }
  
  .file-icon {
    margin-right: 5px;
  }
  
  
  /* GridA.css */
  
  .grid-container {
    width: 500%;
    font-size: small;
    justify-content: center;
    margin-left: 7rem;
    display: flex;
  }
  
  .file-browser {
    flex: 1;
  }
  
  .tree-folder-header {
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  
  .tree-folder-header-div {
    display: flex;
  }
  
  
  .folder-icon {
    margin-right: 5px;
  }
  
  .item-name {
    margin-right: 10px;
  }
  
  .nested-folder {
    margin-left: 20px;
  }
  
