.backup-destination {
  max-width: 600px; /* Adjust the max-width as needed */
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  height: 460px;
}
.backup-destination h2{
  color: black;
  margin-left: 10%;
}

.folder-path {
display: flex;
}

.path-arrow {
margin: 0 5px;
}
.error-message {
color: red;
margin-top: 5px;
}
.alert {
margin-top: 10px;
padding: 10px;
border: 1px solid #ccc;
background-color: #f8d7da;
color: #721c24;
border-radius: 4px;
}

.alert button {
margin-left: 10px;
}

@media (max-width: 600px) {
.backup-destination {
width: 80%;
margin: 0 auto;
}
}


/* Numtwo.css */

.popup-container {
position: fixed;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
background-color: rgba(255, 255, 255, 0.9);
border-radius: 10px;
padding: 20px;
box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
z-index: 1000;
backdrop-filter: blur(5px); /* Blur effect */
}

.close-icon {
position: absolute;
top: 10px;
right: 10px;
cursor: pointer;
font-size: 20px;
color: #333;
}

/* Blur effect for the background */
.blur-background {
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background-color: rgba(255, 255, 255, 0.5);
backdrop-filter: blur(5px);
}
