/* Tree folder */
.tree-folder-header,
.tree-folder-header-div {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

/* Arrow icon */
.arrow {
  margin-right: 5px;
  cursor: pointer;
}

/* Folder and file icons */
.folder-icon,
.file-icon {
  margin-right: 5px;
  font-size: 20px;
}

/* Item name */
.item-name {
  margin-right: 5px; /* Adjust as needed */
}

/* Nested folder */
.nested-folder {
  margin-left: 20px; /* Adjust margin-left for each level */
  margin-right: 5px; /* Adjust as needed */
}

.nested-folder .nested-folder {
  margin-left: 40px; /* Adjust margin-left for second level */
}

.nested-folder .nested-folder .nested-folder {
  margin-left: 60px; /* Adjust margin-left for third level */
}

/* Checkbox */
input[type='checkbox'] {
  margin-right: 10px;
}

