/* Tree structure */
.ul {
  list-style: none;
  padding-left: 20px; /* Adjust as needed */
}

.tree-folder-header .arrow {
  margin-right: 10px;
}

.tree-folder-header .popup-trigger {
  display: flex;
  align-items: center;
}

.tree-folder-header .popup-trigger .folder-icon,
.tree-folder-header .popup-trigger .file-icon {
  margin-right: 5px;
}

/* Lines for folder hierarchy */
.tree-folder-header::before {
  content: '';
  position: absolute;
  top: 15px;
  left: -15px; /* Adjust as needed */
  width: 15px;
  border-left: 1px solid #ccc;
  height: calc(100% - 15px);
  z-index: -1;
}

/* Opened folder arrow */
.tree-folder-header.opened::before {
  content: '';
  position: absolute;
  top: 15px;
  left: -15px; /* Adjust as needed */
  width: 15px;
  border-left: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  height: calc(100% - 15px);
  z-index: -1;
}

