.HashtagsHome {
    /* margin-top: 5rem;
    margin-left: 5rem; */
    /* padding: 1rem; */
    width: 98%;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;

}

.HashtagContainer {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 102%;
    border: 1px solid #ccc;
    background-color: #ffff;
    margin-left: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    height: auto;

}

.hashtagInput {
    /* min-width: 35%; */
    flex: 1;
    margin-bottom: 0;
    border: none;
    background-color: transparent;
}

input:focus {
    border: none;
}

.HashtagSearchList {
    width: 95%;
    border-top: none;
    background-color: #fff;
    overflow-y: auto;
    max-height: 22rem;
    overflow-x: hidden;
}

.HashtagSelectedContainer {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding: 0 10px;
}

.HashtagSelectedData {
    display: flex;
    gap: 5px;
    align-items: center;
    justify-content: center;
    width: fit-content;
    border: 1px solid #ccc;
    padding: 5px;
    border-radius: 5px;
    margin: 5px;
    cursor: pointer;
    background-color: lightblue;
}
.hashtage-h5{
    font-size: 18px;
    font-weight: 200;
}
.closebtn{
    display: flex;
}
.HashtagSelectedData .SelectIcon{
    width: 30px;
    height: 30px;
}

.HashtagSearchList button {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
    background-color: #fff;
    color: black;
    align-items: center;
    justify-content: space-between;
    /* border: 1px solid; */
    padding: 10px;
}

.HashtagSearchList p {
    display: flex;
    width: 5rem;
    margin: 0;
}

.HashtagSearchList button:hover {
    background-color: #f8f9fa;
}

.highlighted {
    background-color: #d3d3d3 !important;
}

.Selectfile-icon {
    display: flex;
    width: 35px;
    height: 34px;
}
