.total-cards {
    display: flex;
    width: 49%;
    height: 200%;
    /* border: 1px solid #ccc; */
    /* padding: 10px; */
    border-radius: 3px;
    /* background-color: #ffff; */
    margin-top: -14rem;
    margin-left: -5rem;
    cursor: pointer;
    font-size: 25px;
    z-index: 1;
    /* box-shadow: 1px 5px 8px #ccc; */
  }
  
  .total-cards h4 {
    position: absolute;
    margin-top: 1rem;
    justify-content: center;
    margin-left:2.5rem;
    font-size: 20px;
  }

  .total-cards h3 {
    position: absolute;
    justify-content: center;
    /* margin-left: 1.2rem; */
    margin-top: 1rem;
    font-size: 20px;
  }
  .total-cards .pie-chart {
    margin-left: 5rem;
    box-sizing: border-box;
    display: block;
    height: 300px;
    width: 300px;
  }

  .loading-overlayy {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* semi-transparent black */
    z-index: 9999; /* Make sure the overlay is above other content */
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading-indicatorr {
  color: white;
  font-size: 24px;
  max-width: 35%;
}
  