/* About.css */
.header-container {
    margin-top: 8rem;
    display: block;
    text-align: center;
    
}
.header {
    text-align: center;
    margin-top: 20px;
    font-weight: 300;
    color: #568301;
}

.header-scope {
    color: #000;
    font-size: 24px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 2rem;
}

.menus-pageA {
    list-style: none;
    padding: 0;
    margin: 2px;
}



.menu-button {
  color: #277DB0;
  background-color: #ffff;
  padding: 10px 15px;
  margin: 5px;
  border: 1px solid #277DB0;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
}

.menu-button:hover {
  background-color: #277DB0;
  color: #ffff;
}

.menu-button.active {
  background-color: #277DB0;
  color: #ffff;
}

.about-content {
    width: 1000px;
    display: block;
    text-align: center;
    justify-content: center;
    margin-top: 2rem;
    margin-left: 10rem;
}
  
@media screen and (max-width: 480px) {
    .menu-button {
        display: block;
        margin: 1rem;
    }

    .menu-button:hover {
        background-color: #277DB0;
        color: #ffff;
    }
  }

@media screen and (max-width: 766px) {
    .menu-button {
        display: block;
        margin: 1rem;
    }

    .menu-button:hover {
        background-color: #277DB0;
        color: #ffff;
    }
}
.www{
    height: 100px;
    width: 100%;
    background-color: cornflowerblue;
}