/* BackLocation.css */
.restore-location-container {
  width: 100%;
  height: 100%;
  display: grid;
  margin-left: 1rem;
  justify-content: center;
  gap: 1rem;
  padding: 10px;
  border: 1px solid #ccc;
  background-color: #ffff;
  border-radius: 5px;
  box-shadow: 1px 3px 5px #808080;
}



.input-container select {
  width: 400px;
  font-size: 16px;
  font-family: Poppins,sans-serif;
  background: none;
}


.popup-restore {
  width: 81%;
  height: 85%;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  margin-top: 4rem;
  margin-left: 10rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
}

.second-section-restore {
  display: flex;
  justify-content: center;
}

.second-section-restore p {
  width: 25px;
}

.popup-restore h3 {
  text-align: center;
}

.popup-restore .agent-search {
  width: 38%;
  float: inline-end;
  margin: -2rem 37rem 0 0;
  border-radius: 5rem;
  box-shadow: 1px 3px 5px #808080;
}

.popup-restore .agent-search-btn {
  display: flex;
  position: absolute;
  font-size: 20px;
  gap: 16rem;
  margin-left: 21rem;
  margin-top: -1.3rem;
  cursor: pointer;
}


.restore-close-btn {
  position: absolute;
  margin-left: 60rem;
  margin-top: -2rem;
  color: #000;
  font-size: 30px;
}

.restore-daterange-picker input {
  width: 400px;
  /* background: none; */
}

.restore-daterange-picker label {
  margin-left: 1rem;
}

.restore-daterange-picker button {
  float: right;
  justify-content: center;
  background: none;
  color: #000;
  border: 1px solid #ccc;
}

.restore-daterange-picker button:hover {
  color: #ffff;
  background-color: #277Db0;
}

.grid-component {
  height: 360px;
  overflow-y: scroll;
}

.none-data-alert h4 {
  color: red;
  text-align: center;
  margin-top: 10rem;
  font-weight: lighter;
}

.none-data-alert p {
  color: #000;
  text-align: center;
  margin-top: 10rem;
}

.restore-midsec {
  width: 89%;
  display: flex;
  justify-content: center;
  margin-left: 6.2rem;
  gap: 7rem;
}

.restore-midsec h4 {
  justify-content: center;
  color: #000;
  padding: 5px;
  border: 1px solid #ccc;
  background-color: #e7f311;
  font-weight: lighter;
  cursor: pointer;
}

.restore-midsec i {
  padding: 3px;
}

.restore-midsec h4:hover {
  box-shadow: 1px 3px 5px #808080;
}

.grid-component img {
  background: none;
  max-width: 25px;
  margin-left: 1.5rem;
  margin-top: .5rem;
  /* border: 1px solid #ccc; */
}

/* .grid-component button:hover {
  color: #ffff;
  background-color: #277Db0;
} */

.grid-component table {
  width: 90%;
  justify-content: center;
  margin-left: 6rem;
  margin-top: 1rem;
  border: 1px solid #ccc;
  overflow-y: scroll ;
}

.grid-component table, th, td {
  border: 1px solid #ccc;
}

.backup-grid tr td {
  justify-content: center;
  padding: 5px;
}

.backup-grid tbody tr:hover {
  background-color: #fcf7b6;
  box-shadow: 1px 3px 5px #808080;
  cursor: pointer;
}

.backup-grid tbody .selected-row {
  background-color: #eaf248;
  box-shadow: 1px 3px 5px #808080;
  cursor: pointer;
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.computer-content {
  max-width: 400px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.computer-content h5 {
  margin-top: .5rem;
  font-size: 16px;
  font-family: Poppins,sans-serif;
  margin-left: .5rem;
}

.computer-btn {
  display: flex;
  margin-left: 23rem;
  margin-top: 0rem;
}

.computer-btn button {
  height: 5%;
  width: 120px;
  color: #f8f9fa;
  background: none;
  border: none;
  border-radius: 5px;
}

.computer-btn button:hover {
  background: none;
}

.computer-content table {
  width: 90%;
  justify-content: center;
  margin-left: 22.5rem;
  border: 1px solid #ccc;

}

.table th{
    position: sticky;
    background-color: #007bff;
    color: white;
}




