/* Card.css */
.unique-card {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 240px;
  width: 270px;
  background: linear-gradient(#0462e4, #ffffff);
  box-shadow: 0px 6px 6px -3px rgba(255, 255, 255, 0.2),0px 10px 14px 1px rgba(0,0,0,0.14),0px 4px 18px 3px rgba(0,0,0,0.12);
  border-radius: 8px;
  margin-top: 10%;
}

.unique-content {
  display: flex;
  flex-direction: column;
  height: 88%;
  width: 88%;
  background-color: white;
  border-radius: 8px;

}

.unique-top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 20%;
  background-color: rgba(209, 207, 202, 0.831);
}

.unique-top p {
  font-weight: bold;
  font-size: 12px;
}

.unique-color {
  height: 20px;
  width: 20px;
  margin-left: 5px;
  margin-right: 10px;
  border-radius: 50%;
}

.unique-middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40%;
  background: linear-gradient(#adacac, #c4cbd2 75%);
  padding-left: 15px;
  padding-right: 15px;
}

.unique-middle img {
  width: 60%;
}

.unique-middle div > p:first-child {
  margin-bottom: 5px;
  font-size: 12px;
} 

.unique-middle div > p:last-child {
  margin: 0;
  font-weight: bold;
}

.unique-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50%;
  padding-left: 30px;
  padding-right: 30px;
}

.unique-status {
  background-color: blanchedalmond;
  padding: 5px 10px;
}

.unique-paraRJ {
  display: flex;
  align-items: center;
  color: blue;
}

.unique-paraRJ p {
  margin-right: 10px;
}

.unique-cardLength {
  background-color: rgb(10, 8, 8);
  width: 50px;
  height: 30px;
  color: gold;
  text-align: center;
}

.unique-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
}

.unique-button:hover {
  background-color: #0056b3;
}




.crdbtn{
  padding: 10px 20px;
  border: 1px solid #000000d3;
  border-radius: 5px;
  background-color: white;
  color: #000000d3;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, color 0.3s;
}

.crdbtn.active,
.crdbtn:hover {
  background-color: green;
  color: white;
}