/**
 * General variables
 */
 $bdrds: 3px;

 $white: #fff;
 $black: #000;
 $gray: #ccc;
 $salmon: #0462e5;
 $smoky-black: #0e55b9e6;
 
 $ff: 'Montserrat', sans-serif;
 $ff-body: 12px;
 $ff-light: 300;
 $ff-regular: 400;
 $ff-medium: 500;
 
 
 /**
  * General configs
  */
//  * {
//    box-sizing: border-box;
//  }
 
//  body {
//    font-family: $ff;
//    font-size: $ff-body;
//    line-height: 1em;
//  }
   
 button {
   background-color: transparent;
   padding: 0;
   border: 0;
   outline: 0;
   cursor: pointer;
 }
   
 input {
   background-color: transparent;
   padding: 0;
   border: 0;
   outline: 0;
 }
   
 input[type="submit"] {
   cursor: pointer;
 }
     
 input::placeholder {
   font-size: .85rem;
   font-family: $ff;
   font-weight: $ff-light;
   letter-spacing: .1rem;
   color: $gray;
 }
 
 
 /**
  * Bounce to the left side
  */
 @keyframes bounceLeft {
   0% {
     transform: translate3d(100%, -50%, 0);
   }
   50% {
     transform: translate3d(-30px, -50%, 0);
   }
   100% {
     transform: translate3d(0, -50%, 0);
   }
 }
 
 /**
  * Bounce to the left side
  */
 @keyframes bounceRight {
   0% {
     transform: translate3d(0, -50%, 0);
   }
   50% {
     transform: translate3d(calc(100% + 30px), -50%, 0);
   }
   100% {
     transform: translate3d(100%, -50%, 0);
   }
 }
 
 /**
  * Show Sign Up form
  */
 @keyframes showSignUp {
   100% {
     opacity: 1;
     visibility: visible;
     transform: translate3d(0, 0, 0);
   }
 }
 
 /**
  * Page background
  */
 .user {
   display: flex;
   justify-content: center;
   align-items: center;
 
   width: 100%;
   height: 100vh;
   background: #eae8e8;
   background-size: cover;
 }
 
 .user_options-container {
   position: relative;
   width: 80%;
 }
 
 .user_options-text {
   display: flex;
   justify-content: space-between;
   width: 100%;
   background-color: $smoky-black;
   border-radius: $bdrds;
 }
 
 /**
  * Registered and Unregistered user box and text
  */
 .user_options-registered,
 .user_options-unregistered {
   width: 50%;
   padding: 75px 45px;
   color: $white;
   font-weight: $ff-light;
 }
 
 .user_registered-title,
 .user_unregistered-title {
   margin-bottom: 15px;
   font-size: 1.66rem;
   justify-content: flex-start;
   line-height: 1em;
   color: #fff;
 }
 
 .user_unregistered-text,
 .user_registered-text {
   font-size: .83rem;
   line-height: 1.4em;
 }
 
 .user_registered-login,
 .user_unregistered-signup {
   margin-top: 30px;
   border: 1px solid $gray;
   border-radius: $bdrds;
   padding: 10px 30px;

   text-transform: uppercase;
   line-height: 1em;
   letter-spacing: .2rem;
   transition: background-color .2s ease-in-out, color .2s ease-in-out;
 }
 
 .user_registered-login:hover,
 .user_unregistered-signup:hover {
   color: $smoky-black;
   background-color: $gray;
 }
 
 /**
  * Login and signup forms
  */
 .user_options-forms {
   position: absolute;
   top: 50%;
   left: 30px;
   width: calc(50% - 30px);
   min-height: 420px;
   background-color: $white;
   border-radius: $bdrds;
   box-shadow: 2px 0 15px rgba($black, .25);
   overflow: hidden;
   transform: translate3d(100%, -50%, 0);
   transition: transform .4s ease-in-out;
 }
 
 .user_forms-login {
   transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
 }

 .user_forms-login img {
  margin-left: 42%;
 }
 
 .forms_title {
   margin-bottom: 45px;
   font-size: 1.5rem;
   font-weight: $ff-medium;
   line-height: 1em;
   text-transform: uppercase;
   color: $salmon;
   letter-spacing: .1rem;
 }
 
 .forms_field:not(:last-of-type) {
   margin-bottom: 20px;
 }
 
 .forms_field-input {
   width: 100%;
   border-bottom: 1px solid $gray;
   padding: 6px 20px 6px 6px;
   font-family: $ff;
   font-size: 1rem;
   font-weight: $ff-light;
   color: darken($gray, 30%);
   letter-spacing: .1rem;
   transition: border-color .2s ease-in-out;
 }
 
 .forms_field-input:focus {
   border-color: darken($gray, 30%);
 }
 
 .forms_buttons {
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-top: 35px;
   
 }
 
 .forms_buttons-forgot {
   font-family: $ff;
   letter-spacing: .1rem;
   color: $gray;
   text-decoration: underline;
   transition: color .2s ease-in-out;
 }
 
 .forms_buttons-forgot:hover {
   color: darken($gray, 10%);
 }
 
 .forms_buttons-action {
   background-color: $salmon;
   border-radius: $bdrds;
   padding: 10px 35px;
   font-size: 1rem;
   font-family: $ff;
   font-weight: $ff-light;
   color: $white;
   text-transform: uppercase;
   letter-spacing: .1rem;
   transition: background-color .2s ease-in-out;
   background-color: #0462e5;
 }
 
 .forms_buttons-action:hover {
   background-color: darken($salmon, 10%);
 }
 
 .user_forms-signup,
 .user_forms-login {
   position: absolute;
   top: 70px;
   left: 40px;
   width: calc(100% - 80px);
   opacity: 0;
   visibility: hidden;
   transition: opacity .4s ease-in-out, visibility .4s ease-in-out, transform .5s ease-in-out;
 }

 .user_forms-signup {
  margin-top: -13%;
 }
 
 
 .user_forms-signup {
   transform: translate3d(120px, 0, 0);
 }

 .user_forms-signup img {
  margin-left: 42%;
 }
 
 .user_forms-signup .forms_buttons {
   justify-content: flex-end;
 }
 
 .user_forms-login {
   transform: translate3d(0, 0, 0);
   opacity: 1;
   margin-top: -5%;
   visibility: visible;
 }
 
 /**
  * Triggers
  */
 .user_options-forms.bounceLeft {
   animation: bounceLeft 1s forwards;
 
   .user_forms-signup {
     animation: showSignUp 1s forwards;
   }
 
   .user_forms-login {
     opacity: 0;
     visibility: hidden;
     transform: translate3d(-120px, 0, 0);
   }
 }
 
 .user_options-forms.bounceRight {
   animation: bounceRight 1s forwards;
 }
 
 /**
  * Responsive 990px
  */
 @media screen and (max-width: 990px) {
   .user_options-forms {
     min-height: 350px;
   }
 
   .forms_buttons {
     flex-direction: column;
   }
 
   .user_forms-login .forms_buttons-action {
     margin-top: 30px;
   }
 
   .user_forms-signup,
   .user_forms-login {
     top: 40px;
   }
 
   .user_options-registered,
   .user_options-unregistered {
     padding: 50px 45px;
   }
 }
 

 .forms_fieldset{
  border: none;
 }