/* Basic styling for desktop view */
.restore-backup-config {
    max-width: 400px;
    margin: 10% auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
  }
  
  .backup-list,
  .passphrase-input,
  .file-input {
    margin-bottom: 20px;
  }
  
  /* Responsive styling for smaller screens */
  @media screen and (max-width: 600px) {
    .restore-backup-config {
      max-width: 100%;
    }
  }
  