/* TableExample.css */

.grid-containerrs {
  width: 150%;
  display: flex;
  margin-left: 6rem;
  margin-top: 1rem;
}

.grid-componentts h2{
  display: flex;
  text-align: center;
  margin-top: 5rem;
}

grid-componentts select {
  margin-top: -5rem;
}

.grid-tableeg {
  width: 100%;
  border-collapse: collapse;
  overflow-x: scroll;
}

.grid-tableeg th, .grid-tableeg td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: justify;
}

.grid-tableeg th {
  background-color: #3498db;
  color: #fff;
}

.page-size   {
  width: 60px;
  margin-left:7rem;
  margin-top: -5rem;
}

.page-size label {
  display: flex;
  width: 10rem;
  margin-top: 2.7rem;
}

.page-size select {
  display: flex;
  margin-top: -.5rem;
}

.new-btn {
  display: flex;
  position: fixed;
  justify-content: center;
  margin-left: 8rem;
}

.page-number {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #3498db;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0 5px;
}

.page-number.active {
  background-color: #2980b9;
}



.new-backup-btn,
.edit-backup-btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}


.new-backup-btn:hover,
.edit-backup-btn:hover {
  background-color: #0056b3;
}


.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.grid-componentts .new-btn {
  position: absolute;
  display: flex;
  color: #000000;
  justify-content: center;
  margin-top: -2.6rem;
  margin-left: 64rem;
  background: none;
  border: 1px solid #000000;
}

.grid-componentts .new-btn:hover {
  color: #ffff;
  background-color: #3498db;
}
