.add-container {
  margin-top: 5rem;
}

.indexedStep {
  color: rgb(190, 190, 190);
  width: 30px;
  height: 30px;
  font-size: 12px;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  border-color: rgb(206, 206, 206);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.indexedStep.accomplished {
  background-color: #664DE5;
  color: white;
  border-style: none;
}

.RSPBprogressBar {
  height: 2px;
  width: 30%;
  line-height: 1;
  border-radius: 10px;
  position: relative;
  background-color: rgb(207, 207, 207);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 0;
  margin: 20px auto;
}

.RSPBprogressBar .RSPBstep {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  z-index: 0;
  position: absolute;
  transform: translateX(-50%);
  transition-property: all;
  transition-timing-function: ease;
}

.RSPBprogressBar .RSPBprogressBarText {
  color: white;
  font-size: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.RSPBprogressBar .RSPBprogression {
  position: absolute;
  transition: width 0.3s ease;
  left: 0;
  top: 0;
  bottom: 0;
  border-radius: 10px;
  background: #664DE5;
  z-index: -1;
}

.step-text {
  display: flex;
  justify-content: center;
}

.buttons-container {
  display: flex;
  justify-content: center;
  margin-right: 0%;
  margin-top: 1rem;
  gap: 50rem;
}

.buttons-container button {
  color: #000;
  background: none;
  justify-content: center;
  border: 1px solid #ccc;
}

.buttons-container button:hover {
  color: #ffff;
  background-color: #277Db0;
}

@media screen and (max-width: 480px) {
  .indexedStep{
      width: 15px;
      height: 15px;
      font-size: 6px;
  }
}