/* General Styles */

/* Set root variables for reusable colors and spacing */
:root {
    --primary-color: #007bff; /* Primary color for buttons and headers */
    --container-color: #fff; /* Background color for containers */
    --text-color: #000; /* Default text color */
    --border-color: rgba(0, 0, 0, 0.1); /* Border color for elements */
    --font-size: 16px; /* Default font size */
    --font-medium: 500; /* Medium font weight */
    --spacing-unit: 1rem; /* Default spacing unit */
  }
  
  body {
    font-family: Arial, sans-serif; /* Set a default font for the body */
    margin: 0;
    padding: 0;
    font-size: var(--font-size);
    color: var(--text-color);
    background-color: #f8f9fa; /* Light background color */
  }
  
  /* Utility Classes */
  
  .container {
    max-width: 1200px; /* Set maximum width for content */
    margin: 0 auto; /* Center align the container */
    padding: var(--spacing-unit); /* Add padding around the container */
  }
  
  .grid {
    display: grid;
  }
  
  /* Section Styles */
  
  .section {
    padding: var(--spacing-unit) 0; /* Add vertical padding to sections */
  }
  
  .section__title {
    margin-top: 3rem;
    font-size: 2rem; /* Larger font size for section titles */
    margin-bottom: var(--spacing-unit); /* Add space below section titles */
    color: var(--primary-color); /* Use primary color for section titles */
  }
  
  /* Services Container Styles */
  
  .services__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Responsive grid columns */
    gap: var(--spacing-unit); /* Add gap between grid items */
    justify-items: center; /* Center align grid items */
  }
  
  .services__content {
    position: relative;
    border: 1px solid var(--border-color);
    background-color: var(--container-color);
    padding: var(--spacing-unit); /* Add padding inside content */
    border-radius: 1rem;
    text-align: center; /* Center align content inside the container */
  }
  
  .services__icon {
    font-size: 2rem; /* Increase icon size */
    margin-bottom: var(--spacing-unit); /* Add space below icons */
    color: var(--primary-color); /* Use primary color for icons */
  }
  
  .services__title {
    font-size: 1.5rem; /* Increase title font size */
    margin-bottom: var(--spacing-unit); /* Add space below titles */
    font-weight: var(--font-medium); /* Use medium font weight for titles */
  }
  
  .services__button {
    color: var(--primary-color); /* Use primary color for buttons */
    font-size: 1rem; /* Set button font size */
    display: inline-flex;
    align-items: center;
    gap: 0.25rem; /* Add gap between button text and icon */
    cursor: pointer;
  }
  
  .services__button-icon {
    font-size: 1rem; /* Set icon size */
    transition: transform 0.3s; /* Add smooth transition */
  }
  
  .services__button:hover .services__button-icon {
    transform: translateX(0.25rem); /* Move icon slightly on hover */
  }
  
  /* Services Modal Styles */
  
  .services__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    z-index: 1000; /* Ensure modal appears above other content */
    display: flex;
    justify-content: center;
    align-items: center;
    padding: var(--spacing-unit); /* Add padding inside modal */
    opacity: 0;
    visibility: hidden;
    transition: 0.3s; /* Add smooth transition */
  }
  
  .services__modal.active-modal {
    opacity: 1; /* Make modal visible */
    visibility: visible;
  }
  
  .services__modal-content {
    max-width: 500px; /* Set maximum width for modal content */
    background-color: var(--container-color);
    padding: var(--spacing-unit); /* Add padding inside modal content */
    border-radius: 1rem;
    position: relative;
  }
  
  .services__modal-close {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    font-size: 1.5rem;
    color: var(--primary-color); /* Use primary color for close icon */
    cursor: pointer;
  }
  
  .services__modal-title {
    font-size: 1.5rem; /* Set title font size */
    font-weight: var(--font-medium); /* Use medium font weight for titles */
    margin-bottom: var(--spacing-unit); /* Add space below title */
  }
  
  /* Form Styles */
  .form-label{
    float: left;
    margin: 1rem 2rem;
    
  }
  
  .form-input{
    width: 85%; /* Set input width to full */
    padding: 0.5rem; /* Add padding inside inputs */
    margin-bottom: var(--spacing-unit); /* Add space below inputs */
    border: 1px solid var(--border-color);
    border-radius: 0.5rem;
  }
  
  .submit-button {
    width: 50%; /* Set button width */
    padding: 0.5rem; /* Add padding inside button */
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    margin-top: var(--spacing-unit); /* Add space above button */
    background-color: var(--primary-color); /* Use primary color for button background */
    color: #fff; /* White text color */
    transition: background-color 0.3s; /* Add smooth transition */
  }
  
  .submit-button:hover {
    background-color: #0056b3; /* Darken button color on hover */
  }
  
  /* Table Styles */
  
  .custom-table1 {
    width: 90%; /* Set table width to full */
    margin-top: var(--spacing-unit); /* Add space above table */
    margin-left: 8rem;
    border-collapse: collapse; /* Collapse borders between table cells */
  }
  
  .custom-table1 th,
  .custom-table1 td {
    border: 1px solid var(--border-color);
    padding: 0.5rem; /* Add padding inside table cells */
  }
  
  .custom-table1 th {
    background-color: var(--primary-color); /* Use primary color for table headers */
    color: #fff; /* White text color */
    font-weight: var(--font-medium); /* Use medium font weight for headers */
  }
  
  /* Responsive Design */
  
  @media screen and (max-width: 767px) {
    .services__container {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* Adjust grid columns for smaller screens */
    }
  
    .services__modal-content {
      max-width: 80%; /* Adjust modal width for smaller screens */
    }
    .custom-table1 {
        width: 90%; /* Set table width to full */
        overflow-x: scroll;
        display: block;
        margin-top: var(--spacing-unit); /* Add space above table */
        margin-left: 1rem;
        border-collapse: collapse; /* Collapse borders between table cells */
      }
  }
  
  @media screen and (max-width: 576px) {
    .services__modal-content {
      padding: var(--spacing-unit); /* Adjust padding inside modal content */
    }
  
}

@media screen and (max-width: 1199px) {
    .container {
      padding: calc(var(--spacing-unit) / 2);
    }
    .custom-table1 {
        width: 80%;
        margin-top: var(--spacing-unit);
        margin-left: 8rem;
        border-collapse: collapse;
      }
  }

  @media screen and (max-width: 1280px) {
