

.Numfive {
    max-width: 600px; /* Adjust the max-width as needed */
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }

  
  
  .button-container {
    display: flex;
    flex-wrap: wrap;
  }
  
  .buttoninc {
    flex: 1;
    margin: 10px;
    text-align: center;
  }
  
  .button-group {
    display: flex;
    align-items: center;
  }
  
  button {
    margin: 5px 5px;
  }
  
  @media (max-width: 600px) {
    .button-container {
      flex-direction: column;
    }
  }
  